    this.selectTime(1);
    this.json = this.getRespone.mechanicRank.slice(0, 3);
    this.jsonTwo = this.getRespone.mechanicRank.slice(
      3,
      this.getRespone.mechanicRank.length
    );
  },
  methods: {
    selectTime(e) {
      var year = this.util.getTime(new Date()).substring(0, 4);
      var month = this.util.getTime(new Date()).substring(5, 7);
      var day = this.util.getTime(new Date()).substring(8, 10);

      this.yearAchtive = e;
    }
  },
  components: {
    tab
  }
};
</script>

